.card{
  border: px solid white;
  width:300px;
  height:300px;
  margin:10px;
  box-shadow: 0 0 20px rgb(196, 160, 94);
  cursor: pointer;
  background-color: rgb(255, 251, 240);
  border-radius: 10px;
}

.container{
  margin-left:40px;
}

.imgCaption{
 position: relative;
 top:30px;
 left:95px;
}

.imgCaption2{
  position:relative;
  right:100px;
  bottom:20px;
  background-color:rgb(194, 214, 196);
  z-index: 1;
  word-wrap: initial;
  color:black;
  margin-bottom: 20px;

}

.image{
  max-width:100%;
 height: auto;
 border-radius: 10px;
  
}

.imageHori{
  max-width:auto;
 height:100%;
 position:relative;
 left:50px;

  
}

.listView
{display:flex;
 flex-direction:column;
 align-items:center;
 
}

.gridView
{
  display:grid;
  grid-template-columns :repeat(3,auto);
  grid-template-rows:repeat(3,auto);
}

@media(max-width:500px){
  .card{
   
    width:100px;
    height:100px;
    border-radius: 0px;
   /*  margin:10px; */
  }
  .container{
    margin-left:0px;
  }
  .image{
   border-radius: 0px;
  }
  .imgCaption{
    position: relative;
    top:0px;
    left:13px;
    font-size: 10px;
   }

   .imgCaption2{
    position:relative;
    right:-25px;
    bottom:30px;
    font-size: 10px;
    background-color:rgb(194, 214, 196);
    margin-bottom: 0px;
  }

  .imageHori{
    max-width:auto;
   height:100%;
   position:relative;
   left:20px;
  }

}

@media(min-width:501px) and (max-width:768px){
  
  .card{
   
    width:100px;
    height:100px;
    border-radius: 0px;
   
  }
  .imgCaption{
    position: relative;
    top:0px;
    left:13px;
    font-size: 10px;
   }

   .imgCaption2{
    position:relative;
    right:-25px;
    bottom:30px;
    font-size: 10px;
    background-color:rgb(194, 214, 196);
    margin-bottom: 0px;
  }

  .image{
    border-radius: 0px;
   }

   .imageHori{
    max-width:auto;
   height:100%;
   position:relative;
   left:20px;
  }
}