.modal{
  
  position: fixed;
  left: 0;
  top: 0;
 height: 100%;
 width: 100%;
/*   overflow: auto; */
  z-index: 1;
  background-color: rgba(15, 15, 15, 0.5);
  cursor:auto;
}

.modalContent{
  background-color: white;
  max-width:70%;
 max-height:90%; 
  margin: auto;
  margin-top: 35px;
 }
 .modalContent2{
  background-color: white;
 /*  min-width:40%; */
 min-width: fit-content;
  max-width: fit-content;
  height:90%; 
  margin: auto;
  margin-top: 35px;
  

 
 }
.header{
  background-color:rgb(194, 214, 196);
  max-height:50px;
  
  text-align: center;
  color:white;
  line-height: 50px;
}


.closebtn{
  float:right;
  padding:10px;
  cursor:pointer;
  background-color: rgb(236, 194, 131);
  color: white;
  border:none;
  outline: none;
  border-radius: 8px;
  margin-top: 7px;
  margin-right: 5px;
}

.imagezoom{
  max-width:100%;
 height: auto;
}

.imagezoomHori{
 max-height:480px;
 max-width:550px;
}
.caption{
  color:black;
  font-weight: bold;
  vertical-align: middle;
  display:inline-block;
}

.caption2{
  color:black;
  font-weight: bold;
  vertical-align: middle;
  display:inline-block;
}
@media(max-width:500){

 
  .imagezoomHori{
    left:0px;
  }
  .modal{
  
    position: fixed;
    left: 0;
    top: 0;
  max-height: 100%;
   max-width: 100%;
    overflow: auto;
    z-index: 1;
    background-color: rgba(15, 15, 15, 0.5);
    cursor:auto;
}
}