.container{
  
 background:url('./fur.jpg');
 height:540px;
}

.container2{
  text-align: center;
  padding-top: 20px;
  padding-left: 80px;
  padding-right: 80px;
  color:BLACK;
}

.box1{
  margin-top: 30px;
  margin-left: 10px;
  border: 1px solid rgb(255, 251, 240);
  border-radius: 5px;
  background-color: rgb(255, 251, 240);
  width: 15%;
  height:30%;
  margin-bottom: 10px;
}

.heading1{
  text-align: center;
  margin-top:5px;
  margin-bottom: 0px;
  color:rgb(100, 145, 103) ;
 
}
ul{
  list-style-image: url('./list.png');
  list-style-position: inside;
}
ul li {
  margin-bottom: 5px;
}

.flexContainer{
  display: flex;
  justify-content: space-around;
}

@media(max-width: 500px){
  .container2{
    text-align:justify;
    padding-top: 10px;
    padding-left: 20px;
    padding-right: 20px;
    
  }
  .flexContainer{
    display: block;
  }
  .container{
   height:100%;
   background:url('./fur3.jpg');
  }
  .box1{
    margin-left: 25%;
    width: 50%;
    height:30%;
  }
}

@media(min-width:501px) and (max-width:768px){

  .box1{
    margin-top: 10px;
    margin-left: 4px;
    margin-right: 4px;
    width: 25%;
    height:20%;
    margin-bottom: 50px;
  }

  .container{
  
    background:url('./fur.jpg');
    height:600px;
    margin-bottom: -70px;

   }
}