.container{
  background-color:rgb(255, 251, 240);
 width: 100%;
 display: flex;
 margin-top: -10px;
 align-items: center;
 justify-content: center;
 align-content: space-around;
}

.logo
{
  width:15%;
  height:15%;
  margin-top: -15px;
  margin-bottom: -15px;
  margin-left:  30px;
  margin-right: 55px;

}

.homeIcon{
  color:rgb(131, 153, 133);
  
 
}

.galleryIcon{
  color:rgb(131, 153, 133);
  
}

.home{
  position:relative;
  top:35px;
  left:-46px;
  text-decoration: none;
}

.gallery{
  position: relative;
  top:35px;
  left:-46px;
}
.link{
  text-decoration: none;
  color:black;
}

@media (max-width: 500px) {
  .container{
    margin-top: 0px;
    padding:10px;
  }
  
  .home{
  
  display: none;
  }
  .gallery{
    display: none;
  }
  .logo
{
  width:20%;
  height: 20%;
  margin-right: 35px;

}
}

@media(min-width:501px) and (max-width:768px){
  .home{
     display: none;
    }
  .gallery{
      display: none;
    }
  .container{
      margin-top: 0px;
      padding:10px;
    }
 .logo
    {
      width:20%;
      height: 20%;
      margin-right: 35px;
    
    }
  
}